/* Extend Bootstrap forms, adjustment corners and colors */

.input-group .form-control,
select.form-control,
.form-control {
    box-shadow: none;
    border-radius: 0;
}

.input-group-addon {
    border-radius: 0;
    color: $text-dark;
    background: $white;
}

/* Extend Bootstrap alert, removes the round corners. */

.alert {
    border-radius: 0;
}

/* Extend Bootstrap label, style used in the Release notes. */

span.label {
    font-size: 11px;
    font-weight: 400;
    padding: 4px 7px;
    border-radius: 0;
}
